<template>
  <div class="MainSearch">
    <h1>{{ msg }}</h1>
    <form id="searchForm" action="#" @submit="submitSearch()">
      <input type="search" v-model="searchValue" placeholder="Search Address or Business Name" />
      <input type="submit" value="Search" />
    </form>
  </div>
</template>

<script>
export default {
  name: 'MainSearch',
  props: {
    msg: String
  },
  data() {
    return {
      searchValue: ''
    }
  },
  methods: {
    submitSearch() {
      // alert(`before emit, search: ${this.searchValue}`);
      this.$emit('search', this.searchValue);
    }
  }
}
</script>

